/* The border for all the parts of the audio player. */
.rm-brd {
  border: 1px solid #3A3B3C;
  border-bottom: none; }
  .rm-brd:last-child {
    border: 1px solid #3A3B3C; }

/* The background for all the main parts of the audio player. */
.rm-bg-primary {
  background-color: #0090FF; }

/* The background for the information about a musician. */
.rm-bg-secondary {
  background-color: #F8F8F8; }

.rm-title-level1, .rm-title-level2, .rm-title-level3 {
  color: #F8F8F8;
  font-weight: bold;
  font-size: 25px; }

.rm-title-level2, .rm-title-level3 {
  font-size: 20px; }

.rm-title-level3 {
  font-weight: normal;
  font-size: 15px; }

.rm-flex {
  display: flex;
  justify-content: space-between; }

#rm-radio-station {
  margin: 10px; }

#rm-musician-image-field {
  display: flex;
  justify-content: center;
  align-items: center; }
  #rm-musician-image-field img {
    display: block;
    max-width: 100%; }

#rm-musician-information {
  margin: 10px 0;
  text-align: center; }
